// 测试SSE功能的简化版本
export interface Env {
  OPENAI_API_KEY: string
  CORS_ORIGIN?: string
}

const corsHeaders = {
  'Access-Control-Allow-Origin': '*',
  'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE, OPTIONS',
  'Access-Control-Allow-Headers': 'Content-Type, Authorization',
  'Access-Control-Max-Age': '86400',
}

async function handleSSE(request: Request, env: Env) {
  const url = new URL(request.url)
  const messageId = url.pathname.split('/')[2]
  const userMessage = url.searchParams.get('message') || ''

  console.log(`SSE request: messageId=${messageId}, message=${decodeURIComponent(userMessage)}`)

  const { readable, writable } = new TransformStream()
  const writer = writable.getWriter()

  const streamResponse = async () => {
    try {
      // 模拟AI响应
      const aiResponses = [
        "你好！我是AI助手。",
        "\n\n让我来回答你的问题：",
        "\n\n## 关于你的问题\n\n",
        "这是一个很好的问题。我建议：\n\n",
        "1. 首先了解基本概念\n",
        "2. 然后进行实践\n",
        "3. 最后总结经验\n\n",
        "希望这些建议对你有帮助！"
      ]

      for (let i = 0; i < aiResponses.length; i++) {
        const chunk = aiResponses[i]
        const streamData = {
          content: chunk,
          isComplete: i === aiResponses.length - 1,
          messageId
        }
        
        console.log(`Sending chunk ${i + 1}:`, chunk)
        await writer.write(new TextEncoder().encode(`data: ${JSON.stringify(streamData)}\n\n`))
        
        // 模拟延迟
        await new Promise(resolve => setTimeout(resolve, 800))
      }

    } catch (error) {
      console.error('Streaming error:', error)
      const errorData = {
        content: `抱歉，遇到错误：${error.message}`,
        isComplete: true,
        messageId,
        error: true
      }
      await writer.write(new TextEncoder().encode(`data: ${JSON.stringify(errorData)}\n\n`))
    } finally {
      await writer.close()
    }
  }

  // 立即开始流式响应
  streamResponse()

  return new Response(readable, {
    headers: {
      ...corsHeaders,
      'Content-Type': 'text/event-stream',
      'Cache-Control': 'no-cache',
      'Connection': 'keep-alive',
    },
  })
}

export default {
  async fetch(request: Request, env: Env, ctx: any): Promise<Response> {
    const url = new URL(request.url)

    if (url.pathname.startsWith('/sse/')) {
      return handleSSE(request, env)
    }

    return new Response('Test SSE API', {
      headers: corsHeaders
    })
  },
}